.mylog{
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.ut{
    height: 200px;
}
.logins{
    padding: 10px;
    margin: 10px;
    background-color:lightcoral;
    border: 1px solid black;
    border-width: 0px;
    border-radius: 3px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #fff;
}
.main{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.profiles{
    height: 80vh;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 5px 0px #aeaeae;
}
.proimg{
    border-radius: 50%;
    margin: 10px;
}
.proname{
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
}
.proemail{
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
}
.mass{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.poo{
    font-family: cursive;
}
