.autoBanner{
    height: 100vh;
    background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.7)),url("https://images.pexels.com/photos/4458554/pexels-photo-4458554.jpeg?auto=compress&cs=tinysrgb&w=600");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
}
.buttsd{
    padding: 10px;
    margin: 10px;
    border: 1px solid black;
    border-radius: 30px;
    border-width: 0px;
    background-color: lightsalmon;
    color: black;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.magic{
    height: 100vh;
}
.biolink{
    padding: 10px;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
a{
    text-decoration: none;
    color: black;
}
.bio{
    padding: 10px;
    margin: 10px;
    border: 1px solid black;
}
.Outlite{
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.active{
    background-color: red;
}
@media (max-width:991px) {
    .bann{
        font-size: 17px;
    }
}    