*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
:root{
    --mainColor: #29335c;
    --mainColorLight:#5767aa;
    --secondaryColor:#DB2B39;
    --textColor:#eee;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2 rem;
    background-color: var(--mainColor);
    color: var(--textColor);
}
ul{
    display: flex;
}
li{
    list-style: none;
    padding: 10px;
}
a{
    padding: 10px;
     border-radius: 30px;
}
a:hover{
    background-color: red;
    border-radius: 30px;
    padding: 10px;
}
.active{
    background-color: red;
}
ul li a{
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
}
ul li:hover{
    color: var(--secondaryColor);
}
nav .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity:0 ;
    font-size: 1.8rem;
}
@media (max-width:991px) {
    nav .nav-btn{
        visibility: visible;
        opacity: 1;
    }
    nav ul{
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap:1.5rem;
        background-color: var(--mainColor);
        transition: 1s;
        transform: translateY(-100vh);
    }
    nav .responsive_nav{
        transform: none;
    }
    ul .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;
    }
    ul li a{
        font-size: 1.5 rem;
    }
    
    
}