.pic{
    width:100%
}
.man{
    display: flex;
    justify-content: center;
    align-items: center;
}
.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .container {
    padding: 2px 16px;
  }
.manoj{
    background-color: lightcoral;
    color: black;
    border: 1px solid black;
    border-radius: 5px;
    border-width: 0px;
    padding: 10px;
    margin: 10px;
}  
@media (max-width:991px) {
    .card {
        width: 80%;
    }
}