.Banner{
    height: 100vh;
    background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.7)),url(https://images.pexels.com/photos/4491461/pexels-photo-4491461.jpeg?auto=compress&cs=tinysrgb&w=600);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
}
.butts{
    padding: 10px;
    margin: 10px;
    border: 1px solid black;
    border-radius: 30px;
    border-width: 0px;
    background-color: lightsalmon;
    color: black;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.Time{
    height: 100vh;
}
.Heading{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.para{
    height: 7px;
    width: 100px;
    background-color: lightcoral;
    border-radius: 30px;
}
.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
  }
  
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
.container {
    text-align: center;
    padding: 2px 16px;
  }
.name{
    background-color: lightcoral;
    padding: 10px;
    border: 1px solid black;
    border-radius: 30px;
    border-width: 0px;
  }
.imgesw{
    width: 100%;
}
.cards{
    display: flex;
}
.card{
    margin: 40px;
}
.footer{
    margin-top: 30vh;
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: lightgray;
    box-shadow: 5px 5px 5px 5px #aeaeae;
}
.icon{
    height: 40px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 3s;
}
.icon:hover{
    transform: translateY(-40px);
}
.links{
    display: flex;
}
.facebook{
    border-radius: 50%;
    width: 60px;
    margin: 10px;
}
.pas{
    padding: 10px;
}
@media (max-width:991px) {
    .ban{
        font-size: 10px;
    }
    .cards{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .card {
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
        width: 70%;
    }
    .footer{
        margin-top: 60vh;
        height: 20vh;
    }
}